
import hljs from "highlight.js";
import { defineComponent } from "vue";

export default defineComponent({
  name: "Code",
  props: {
    code: { type: String, required: true },
  },
  methods: {
    highlight() {
      const block = this.$refs.codeBlock as HTMLElement;
      hljs.highlightBlock(block);
    },
  },
  mounted() {
    this.highlight();
  },
  updated() {
    this.highlight();
  },
});
