
import { defineComponent } from "vue";

import Checkbox from "@/components/Checkbox.vue";
import Help from "@/components/Help.vue";
import Results from "@/components/Results.vue";
import TextInput from "@/components/TextInput.vue";

import { genericBadge } from "@/core/genericBadge";
import { COLOR } from "@/constants/appearance";

const note = `
This form lets you create a fixed badge using arbitrary text and a link.

For example, describe a tools or platforms your repo is built on or built for running. This form takes care of encoding characters so they are safe for the badge URL.
`;

export default defineComponent({
  name: "GenericBadges",
  components: {
    Checkbox,
    Help,
    Results,
    TextInput,
  },
  data() {
    return {
      label: "Foo",
      message: "Bar",
      color: COLOR.Green,
      isLarge: false,
      target: "https://",
      logo: "",
      logoColor: "",

      result: "_Your output will appear here_",
      note: note,
    };
  },
  methods: {
    submit() {
      console.debug("Process inputs and render results");

      const args = [
        this.label,
        this.message,
        this.color,
        this.isLarge,
        this.target,
        this.logo,
        this.logoColor,
      ];
      console.debug(args);

      const dashBadge = genericBadge(
          this.label,
          this.message,
          this.color,
          this.isLarge,
          this.target,
          this.logo,
          this.logoColor,
          false
        ),
        paramBadge = genericBadge(
          this.label,
          this.message,
          this.color,
          this.isLarge,
          this.target,
          this.logo,
          this.logoColor,
          true
        );

      this.result = `\
_Dash badge_

${dashBadge}

_Query parameter badge_

${paramBadge}
      `;
    },
  },
});
